import React, { useEffect, useState,useMemo } from 'react'
import { CustomButton } from '../../globalComponents'
import moment from 'moment-timezone'
import { useDispatch } from 'react-redux'
import { getBadgeColor } from '../../../utils/common'
import CustomTable from '../../reuseableComponents/customTable/CustomTable'

const EmailTemplatedata = (props) => {
    const { TemplateList,isLoading,toggleOffcanvas,currentPage,setCurrentPage,setSelectedTemplateData,onHandleDeleteModal,onHandleViewModal} = props

    const [templateList, settemplate] = useState(TemplateList)

    useEffect(() => {
      settemplate(TemplateList)
    }, [TemplateList])

      const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'template_name', label: 'Template Name' },
        { key: 'email_title', label: 'Subject' },
        { key: 'email_type', label: 'Email For' },
        { key: 'is_active', label: 'Status' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label :'Action' }
      ];

      const updateEmailTemplate = (data) => {
        setSelectedTemplateData(data)
        toggleOffcanvas()
      }

      const scopedSlots = useMemo(() => ({
        sn: (_, index) => (         
           <span>{index+1}</span> 
        ),
        is_active: item => (        
          <span
            className={`badge ${getBadgeColor(item.is_active? "Active":"Inactive")}`}
            // style={{ color: '#fff', padding: x'0.5rem' }}
          >
            {item.is_active? "Active":"Inactive"}
          </span>
        ),
        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        action: item => (
          <div className="actionGroup d-flex gap-2">
            <CustomButton
              text="View"
              handleClick={() => onHandleViewModal(item)}
              classNames="btn btn-success"
            />
            <CustomButton
              text="Update"
              classNames="btn btn-primary"
              handleClick={() => {
                updateEmailTemplate(item);
              }}
            />
            <CustomButton
              text="Delete"
              handleClick={() => onHandleDeleteModal(item?.id)}
              classNames="btn btn-danger"
            />
          </div>
        )
      }), [setSelectedTemplateData, toggleOffcanvas, onHandleDeleteModal,onHandleViewModal]);
  
  return (
    <>
    <CustomTable
        data={templateList?.data}
        fields={fields}
        isLoading={isLoading}
        pagination={true}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        totalPages={templateList?.totalPages}
        tableName={"Email Template"}
        // onSearch={handleSearch}
        scopedSlots={scopedSlots}
        />
    </>
  )
}

export default EmailTemplatedata
