import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux'
import { CustomButton } from '../../../globalComponents';
import { emailTemplateAction } from '../../../../core/resource/actions';
import { toast } from 'react-toastify';
import { addEmailTemplatechema } from '../../../../utils/ValidationSchema';
import { validateSchema } from '../../../../utils/ValidationHelper';
import CustomError from '../../../globalComponents/CustomError';

import Customckediter from './Customckediter'

const CreateAndUpdateEmailTemplate = (props) => {

    const { isOpenEmailType, toggleOffcanvas, getAllEmailTemplalte, setSelectedTemplateData, selectedTemplateData, onHandleViewModals } = props
    const [errors, setErrors] = useState({});
    const dispatch = useDispatch();
    const { getEmailTypePending, getEmailTypeRes, getEmailTypeError, addEmailTemplatePending, addEmailTemplateRes, addEmailTemplateError } = useSelector(state => state.emailtemplate);
    const [isActive, setIsActive] = useState(false);
    const [emailType, setEmailType] = useState([])
    const [editorData, setEditorData] = useState('')
    const [templateId, setTemplateId] = useState('')
    const [dropdownValue, setDropdownValue] = useState('');
    const [bodydata, setBodydata] = useState('')
    const [subemailType, setSubEmailType] = useState([])
    const [field, setfield] = useState([])

    const [addEmailTemplate, setAddEmailTemplate] = useState({
        template_name: '',
        email_title: '',
        email_type: '',
        body: '',
        is_active: '',
        remark: ''
    })

    useEffect(() => {
        setAddEmailTemplate({
            template_name: selectedTemplateData?.template_name || '',
            email_title: selectedTemplateData?.email_title || '',
            email_type: selectedTemplateData.email_type || '',
            body: selectedTemplateData?.body,
            remark: selectedTemplateData?.remark
        })
        setIsActive(selectedTemplateData?.is_active)
        setTemplateId(selectedTemplateData?.id)
        handleAutoFill(selectedTemplateData?.body)
        // setEditorData(selectedTemplateData?.body)
    }, [selectedTemplateData])

    useEffect(() => {
        setAddEmailTemplate({
            template_name: onHandleViewModals?.template_name || '',
            email_title: onHandleViewModals?.email_title || '',
            email_type: onHandleViewModals?.email_type,
            body: onHandleViewModals?.body,
            remark: onHandleViewModals?.remark
        })
        setIsActive(onHandleViewModals?.is_active)
        handleAutoFill(onHandleViewModals?.body)
    }, [onHandleViewModals])

    useEffect(() => {
        getEmailType()
    }, [])

    const getEmailType = () => {
        dispatch(emailTemplateAction.getEmailTypeRequest());
    }

    const newFunction = (data) => {
        setDropdownValue(data?.value)
        field.push(data)

    }
    useEffect(() => {
        if (!getEmailTypePending) {
            if (getEmailTypeError) {
                setEmailType([])
                return;
            }
            if (getEmailTypeRes) {
                if (getEmailTypeRes?.code == 200) {
                    setEmailType(getEmailTypeRes?.data?.data)
                }
            }
        }
    }, [
        getEmailTypePending,
        getEmailTypeRes,
        getEmailTypeError
    ]);

    const handleAutoFill = (data) => {
        setBodydata(data)
    };

    useEffect(() => {
        if (!addEmailTemplatePending) {
            if (addEmailTemplateError) {
                toast.error(addEmailTemplateError?.errorMessage)
                return;
            }
            if (addEmailTemplateRes) {
                if (addEmailTemplateRes?.code === 200) {
                    toggleOffcanvas()
                    getAllEmailTemplalte()
                    dispatch(emailTemplateAction.handleClearEmailTemplateStore("HANDLE_CLEAR_ADD_EMAIL_TEMPLATE_STORE"))
                }
            }
        }
    }, [
        addEmailTemplateRes,
        addEmailTemplateError,
        addEmailTemplatePending
    ]);

    const handleChange = (event) => {
        errors && setErrors({});
        if (event.target.name == 'email_type') {
            setSubEmailType(dropdownOptions?.filter(data => data.task == event.target.value)[0]?.subtasks)
        }
        const { name, value } = event.target
        setAddEmailTemplate({ ...addEmailTemplate, [name]: value })
    }

    const createEmailTemplate = async () => {
        addEmailTemplate.body = editorData
        addEmailTemplate.is_active = isActive
        addEmailTemplate.field = field
        const validationResponse = await validateSchema(addEmailTemplatechema, addEmailTemplate);
        if (validationResponse.isValid) {
            setErrors({});
            // addEmailTemplate.body = editorData
            // addEmailTemplate.is_active = isActive
            if (selectedTemplateData) {
                addEmailTemplate['_id'] = templateId
                dispatch(emailTemplateAction.updateEmailTemplateRequest(addEmailTemplate));
                getAllEmailTemplalte()
                toggleOffcanvas()
            } else {
                dispatch(emailTemplateAction.addEmailTemplateRequest(addEmailTemplate));
                getAllEmailTemplalte()
            }

        } else {
            setErrors(validationResponse.errors);
            console.log(validationResponse.errors, "formattedErrors");
        }
    }

    useEffect(() => {
        if (isOpenEmailType == false) {
            resetTempateData()
        }
    }, [isOpenEmailType])


    const resetTempateData = () => {
        setAddEmailTemplate({
            template_name: '',
            email_title: '',
            email_type: '',
            body: '',
            is_active: ''
        })
        setSelectedTemplateData('')
        setErrors({});
        setEditorData('')
    }
    const dropdownOptions = [
        {
            id: 1,
            task: 'MRF',
            subtasks: [
                { value: '{employee_name}', label: 'Employee Name' },
                { value: '{applying_for}', label: 'Applying for' },
                { value: '{approval_by}', label: 'Approval by' },
                { value: '{mrf_id}', label: 'Mrf id' },
                { value: '{name}', label: 'Name' },
                { value: '{current_date_time}', label: 'Current date time' },
                { value: '{designation}', label: 'Designation' }
            ]
        },
        {
            id: 2,
            task: 'C-off',
            subtasks: [
                { value: '{Name}', label: 'Name' },
                { value: '{emp_name}', label: 'Emp Name' },
                { value: '{date}', label: 'Date' },
                { value: '{no_of_days}', label: 'No of days' },
                { value: '{reason}', label: 'Reason' }
            ]
        },
        {
            id: 3,
            task: 'attendance_correction',
            subtasks: [
                { value: '{manager_full_name}', label: 'manager_full_name' },
                { value: '{emp_name}', label: 'emp_name' },
                { value: '{date}', label: 'date' },
                { value: '{first_check_in}', label: 'first_check_in' },
                { value: '{first_check_out}', label: 'first_check_out' },
                { value: '{corrected_check_in_time}', label: 'corrected_check_in_time' },
                { value: '{corrected_check_out_time}', label: 'corrected_check_out_time' },

            ]
        },
        {
            id: 4,
            task: 'Leave',
            subtasks: [
                { value: '{manager_full_name}', label: 'manager_full_name' },
                { value: '{emp_name}', label: 'emp_name' },
                { value: '{date}', label: 'date' },
                { value: '{first_check_in}', label: 'first_check_in' },
                { value: '{first_check_out}', label: 'first_check_out' },
                { value: '{corrected_check_in_time}', label: 'corrected_check_in_time' },
                { value: '{corrected_check_out_time}', label: 'corrected_check_out_time' },

            ]
        },

        // Add more options as needed
    ];


    return (
        <>
            <div className={`offcanvas offcanvas-end creatNewTicketOff ${isOpenEmailType ? 'show' : ''}`}

                tabIndex={-1}
                style={{
                    visibility: isOpenEmailType ? 'visible' : 'hidden',
                    width: "1025px"
                }}
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">{onHandleViewModals ? "View" : selectedTemplateData ? "Update" : "Create New"} Email Template</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => toggleOffcanvas('view')}
                    />
                </div>
                <div className="offcanvas-body">
                    <div className="formPanel">
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Template Name<span className='text-danger'>{onHandleViewModals == '' && '*'}</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    {onHandleViewModals != '' ?
                                        <div>
                                            {addEmailTemplate?.template_name}
                                        </div> :

                                        <input type="text" className="form-control fs-7"
                                            placeholder="Enter Template Name"
                                            name='template_name'
                                            value={addEmailTemplate?.template_name || ''}
                                            onChange={(e) => handleChange(e)}
                                            disabled={onHandleViewModals != '' ? true : false}
                                        />}

                                    {errors?.template_name && (
                                        <CustomError text={errors.template_name} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Subject<span className='text-danger'>{onHandleViewModals == '' && '*'}</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    {onHandleViewModals != '' ?
                                        <div>
                                            {addEmailTemplate?.email_title}
                                        </div> :

                                        <input type="text" className="form-control fs-7"
                                            name='email_title'
                                            placeholder="Enter Title"
                                            value={addEmailTemplate?.email_title}
                                            onChange={(e) => handleChange(e)}
                                            disabled={onHandleViewModals != '' ? true : false}
                                        />}

                                    {errors?.email_title && (
                                        <CustomError text={errors.email_title} />
                                    )}
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Email For<span className='text-danger'>{onHandleViewModals == '' && '*'}</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    {onHandleViewModals != '' ?
                                        <div>
                                            {addEmailTemplate?.email_type}
                                        </div> :

                                        <select className="form-select" required
                                            name='email_type'
                                            value={addEmailTemplate?.email_type}
                                            onChange={(e) => handleChange(e)}
                                            disabled={onHandleViewModals != '' ? true : false}
                                        >
                                            <option value='' hidden selected>Select Required Email Type</option>
                                            {emailType && emailType?.map(data => (
                                                <option value={data?.name}>{data?.name}</option>
                                            ))}
                                        </select>}

                                    {errors?.email_type && (
                                        <CustomError text={errors.email_type} />
                                    )}
                                </div>
                            </div>
                        </div>
                        {/* <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Email Sub Type<span className='text-danger'>*</span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <select className="form-select" value={dropdownValue} onChange={newFunction}
                                        disabled={onHandleViewModals != '' ? true : false}>
                                      <option value='' hidden selected>Select Required Email Type</option>
                                        {subemailType?.map(option => (
                                            <option key={option.value} value={option.value}>
                                                {option.label}
                                            </option>
                                        ))}
                                    </select>
                                    {errors?.email_type && (
                                        <CustomError text={errors.email_type} />
                                    )}
                                </div>
                            </div>
                        </div> */}
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Body</label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    {onHandleViewModals != '' ?
                                        <div className="cardBox mb-3 mt-3 pt-3 myRequest">
                                            <div>
                                                <div dangerouslySetInnerHTML={{ __html: addEmailTemplate?.body?.replaceAll('&lt;', '<') }} />
                                            </div>
                                        </div> :

                                        <Customckediter
                                            editorData={editorData}
                                            setEditorData={setEditorData}
                                            dropdownValue={dropdownValue}
                                            setDropdownValue={setDropdownValue}
                                            bodydata={bodydata}
                                        />}

                                </div>
                                {errors?.body && (
                                    <CustomError text={errors.body} />
                                )}
                            </div>


                        </div>
                        {onHandleViewModals == '' &&
                            <div className="row">
                                <div className="col-md-3">
                                    <div className="form-group mb-3">
                                        <label htmlFor>Template fields</label>
                                    </div>
                                </div>
                                <div className="col-md-9">
                                    {subemailType?.map(data => (
                                        <span style={{ cursor: 'pointer' }} className="badge blueBadge2 me-2" onClick={() => newFunction(data)}>{data.label}</span>
                                    ))}
                                    {errors?.is_active && (
                                        <CustomError text={errors.is_active} />
                                    )}
                                </div>

                            </div>}
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Re-Mark<span className='text-danger'></span></label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    {onHandleViewModals != '' ?
                                        <div>
                                            {addEmailTemplate?.remark}
                                        </div> :

                                        <input type="text" className="form-control fs-7"
                                            placeholder="Enter ReMark Name"
                                            name='remark'
                                            value={addEmailTemplate?.remark || ''}
                                            onChange={(e) => handleChange(e)}
                                            disabled={onHandleViewModals != '' ? true : false}
                                        />}

                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-3">
                                <div className="form-group mb-3">
                                    <label htmlFor>Is Active</label>
                                </div>
                            </div>
                            <div className="col-md-9">
                                <div className="form-group mb-3">
                                    <input type="checkbox" placeholder="Is Active"
                                        checked={isActive}
                                        onChange={(e) => setIsActive(!isActive)}
                                        disabled={onHandleViewModals != '' ? true : false}
                                    />
                                </div>
                                {errors?.is_active && (
                                    <CustomError text={errors.is_active} />
                                )}
                            </div>

                        </div>
                        <div className="d-flex align-items-center justify-content-end gap-2">
                            {onHandleViewModals == '' &&
                                <CustomButton
                                    type="button"
                                    classNames={"btn btn-primary gradientBg"}
                                    handleClick={e => createEmailTemplate()}
                                    // isLoading={departmentData?updateDepartmentPending:addDepartmentPending}
                                    text={`${selectedTemplateData ? "Update" : "Create"} Template`}
                                />}
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default CreateAndUpdateEmailTemplate
