import React, { useEffect, useState,useMemo } from 'react'
import Button from '../../commanComponent/Button'
import { CustomButton } from '../../globalComponents'
import ApproveRejectModal from '../../modal/ApproveRejectModal'
import moment from 'moment-timezone'
import { toast } from 'react-toastify'
import { CustomSpinner } from '../../globalComponents'
import { useDispatch, useSelector } from 'react-redux'
import { getBadgeColor } from '../../../utils/common'
import CreateAndUpdateEmailTemplate from '../../modal/admin/emailTemplate/CreateAndUpdateEmailTemplate'
import { emailTemplateAction, notificationActions } from '../../../core/resource/actions';
import CustomTable from '../../reuseableComponents/customTable/CustomTable'

const EmailTemplatedata = (props) => {
    const { ActionList,isLoading,toggleOpenAction,currentPage,setCurrentPage,setSelectedActionData,onHandleDeleteAction,onHandleViewModal} = props

    const [actionList, setAction] = useState(ActionList)
    const dispatch = useDispatch();
    const [isOpen, setIsOpen] = useState(false)
    const [viewEmailTemplate, setViewEmailTemplate] = useState([])

    useEffect(() => {
      setAction(ActionList)
    }, [ActionList])

      const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'action_name', label: 'Action Name' },
        { key: 'action_type', label: 'Action Type' },
        { key: 'action_type', label: 'Action Type' },
        { key: 'is_active', label: 'Status' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label :'Action' }
      ];

      const updateAtion = (data) => {
        setSelectedActionData(data)
        toggleOpenAction()
      }

      const scopedSlots = useMemo(() => ({
        sn: (_, index) => (         
           <span>{index+1}</span> 
        ),
        is_active: item => (        
          <span
            className={`badge ${getBadgeColor(item.is_active? "Active":"Inactive")}`}
            // style={{ color: '#fff', padding: x'0.5rem' }}
          >
            {item.is_active? "Active":"Inactive"}
          </span>
        ),
        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        action: item => (
          <div className="actionGroup d-flex gap-2">
            {/* <CustomButton
              text="View"
              handleClick={() => onHandleViewModal(item)}
              classNames="btn btn-success"
            /> */}
            <CustomButton
              text="Update"
              classNames="btn btn-primary"
              handleClick={() => {
                updateAtion(item);
              }}
            />
            <CustomButton
              text="Delete"
              handleClick={() => onHandleDeleteAction(item?.id)}
              classNames="btn btn-danger"
            />
          </div>
        )
      }), [setSelectedActionData, toggleOpenAction, onHandleDeleteAction,onHandleViewModal]);
  
  return (
    <>
    <CustomTable
        data={actionList}
        fields={fields}
        isLoading={isLoading}
        pagination={true}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        totalPages={actionList?.totalPages}
        tableName={"Action"}
        // onSearch={handleSearch}
        scopedSlots={scopedSlots}
        />
    </>
  )
}

export default EmailTemplatedata
