import React, { useEffect, useState, useMemo } from 'react'
import { AddDesignations, DesignationTable } from '../components/admin/mapping/designation'
import Button from '../components/commanComponent/Button'
import { useDispatch, useSelector } from 'react-redux'
import { emailTemplateAction } from '../core/resource/actions'
import { toast } from 'react-toastify'
import { EmailTemplatedata,EmailActionData, EmailTypeData } from '../components/admin/EmailTemplate'
import CreateEmailType from '../components/modal/admin/emailTemplate/CreateEmailType'
import CreateAndUpdateEmailTemplate from '../components/modal/admin/emailTemplate/CreateAndUpdateEmailTemplate'
import { pagination } from '../utils/Constants.config'
import ApproveRejectModal from '../components/modal/ApproveRejectModal'
import TableTabs from '../components/TableTabs'
import AddActionEmailTemplate from '../components/modal/admin/emailTemplate/AddActionEmailTemplate'
import CreateAndUpdateEmailType from '../components/modal/admin/emailTemplate/CreateEmailType'


const EmailTemplate = () => {
  const dispatch = useDispatch()
  const [isOpenEmailType, setIsOpenEmailType] = useState(false);
  const {getActionTemplatePending,
    getActionTemplateRes,
    getActionTemplateError, getEmailTemplatePending, getEmailTemplateRes, getEmailTemplateError, deleteEmailTemplatePending, deleteEmailTemplateRes, deleteEmailTemplateError,
    DeleteActionTemplatePending,
    DeleteActionTemplateRes,
    DeleteActionTemplateError } = useSelector(state => state.emailtemplate);
  const [getemailTemplate, setGetEmailTemplate] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const [selectedTemplateData, setSelectedTemplateData] = useState('')
  const [selectedActionData, setSelectedActionData] = useState('')
  const [deleteDepId, setDeleteDepId] = useState()
  const [isShowDeleteModal, setIsShowDelelteModal] = useState(false)
  const [onHandleViewModals, setonHandleViewModal] = useState('')
  const [openAction, setOpenAction] = useState(false)
  const [openEmailType, setOpenEmailType] = useState(false)
  const [AllGetAction, setAllGetAction] = useState([])
  const [actionDelete,setActionDelete] = useState()
  const [isActionDelete,setIsActionDelete] = useState(false)

  const onHandleViewModal = (data) => {
    setonHandleViewModal(data)
    toggleOffcanvas()
  }

  const onHandleDeleteModal = (id) => {
    setIsShowDelelteModal(!isShowDeleteModal)
    setDeleteDepId(id)
  }

  const onHandleDeleteAction = id =>{
    setIsActionDelete(!isActionDelete)
    setActionDelete(id)
  }

  useEffect(() => {
    getAllEmailTemplalte()
    getAllAction()
  }, [currentPage])

  const getAllEmailTemplalte = () => {
    const params = {
      page: currentPage,
      limit: pagination.limit,
    }
    dispatch(emailTemplateAction.getEmailTemplateRequest(params))
  }

const getAllAction =()=>{
  const params = {
    page: currentPage,
    limit: pagination.limit,
  }
    dispatch(emailTemplateAction.getActionTemplateRequest(params))
}

useEffect(() => {
    if (!getActionTemplatePending) {
        if (getActionTemplateError) {
            setAllGetAction([])
            return;
        }
        if (getActionTemplateRes) {
          console.log("SDfsdfsdfsf", getActionTemplateRes)
            if (getActionTemplateRes?.code == 200) {
                setAllGetAction(getActionTemplateRes?.data)
            }
        }
    }
}, [
    getActionTemplateError,
    getActionTemplateRes,
    getActionTemplatePending
]);

  useEffect(() => {
    if (!getEmailTemplatePending) {
      if (getEmailTemplateError) {
        setGetEmailTemplate([])
        return;
      }
      if (getEmailTemplateRes) {
        if (getEmailTemplateRes?.code == 200) {
          setGetEmailTemplate(getEmailTemplateRes?.data)
        }
      }
    }
  }, [
    getEmailTemplatePending,
    getEmailTemplateRes,
    getEmailTemplateError
  ]);

  useEffect(() => {
    if (!deleteEmailTemplatePending) {
      if (deleteEmailTemplateError) {
        dispatch(emailTemplateAction.handleClearEmailTemplateStore('HANDLE_CLEAR_DELETE_EMAIL_TEMPLATE_STORE'))
        return;
      }
      if (deleteEmailTemplateRes) {
        if (deleteEmailTemplateRes?.code == 200) {
          dispatch(emailTemplateAction.handleClearEmailTemplateStore('HANDLE_CLEAR_DELETE_EMAIL_TEMPLATE_STORE'))
          getAllEmailTemplalte()
        }
      }
    }
  }, [
    deleteEmailTemplateRes,
    deleteEmailTemplateError,
    deleteEmailTemplatePending
  ]);


  useEffect(() => {
    if (!DeleteActionTemplatePending) {
      if (DeleteActionTemplateError) {
        dispatch(emailTemplateAction.handleClearEmailTemplateStore('HANDLE_CLEAR_DELETE_ACTION_TEMPLATE_STORE'))
        return;
      }
      if (DeleteActionTemplateRes) {
        if (DeleteActionTemplateRes?.code == 200) {
          dispatch(emailTemplateAction.handleClearEmailTemplateStore('HANDLE_CLEAR_DELETE_ACTION_TEMPLATE_STORE'))
          getAllAction()
        }
      }
    }
  }, [
    deleteEmailTemplateRes,
    DeleteActionTemplateError,
    DeleteActionTemplatePending
  ]);


  const toggleOffcanvas = (type) => {
    setIsOpenEmailType(!isOpenEmailType);
    if (type == 'view') {
      setonHandleViewModal('')
    }
  };

  const toggleOpenEmailType = ()=>{
    setOpenEmailType(!openEmailType)

  }

  const toggleOpenAction = (type) => {
    setOpenAction(!openAction)
  };

  const onDeleteCancelDepartment = (type) => {
    if (type) {
      const params = {
        _id: deleteDepId
      }
      dispatch(emailTemplateAction?.deleteEmailTemplateRequest(params))
      setIsShowDelelteModal(!isShowDeleteModal)
    } else {
      setIsShowDelelteModal(!isShowDeleteModal)
    }
  }

  const onDeleteActionFunction = (type) => {
    if (type) {
      const params = {
        _id: actionDelete
      }
      dispatch(emailTemplateAction?.DeleteActionTemplateRequest(params))
      setIsActionDelete(!isActionDelete)
    } else {
      setIsActionDelete(!isActionDelete)
    }
  }

  const memoizedTemplateList = useMemo(() => getemailTemplate, [getemailTemplate]);
  console.log("sdfsdfsdf memoizedTemplateList", memoizedTemplateList)
  const memoizedActionList = useMemo(() => AllGetAction, [AllGetAction]);
  return (
      <div className="cardBox mb-3 mt-3 pt-5 myRequest">
        <ul className="nav nav-tabs leaveTabs mb-4">
          <li className="nav-item">
            <button
              className="nav-link active"
              data-bs-toggle="tab"
              data-bs-target="#createEmailTemplate"
              type="button"
            >
              Create Email Template
            </button>
          </li>
          <li className="nav-item">
            <button
              className="nav-link"
              data-bs-toggle="tab"
              data-bs-target="#addAction"
              type="button"
            >
              Add Action
            </button>
          </li>
          {/* <li className="nav-item">
            <button
              className="nav-link"
              data-bs-toggle="tab"
              data-bs-target="#addEmailType"
              type="button"
            >
              Add Email Type
            </button>
          </li> */}
        </ul>
        <div className="tab-content">
          <div className="tab-pane fade active show" id="createEmailTemplate">
            <div className="cardHead d-flex align-items-center justify-content-between">
              <Button btnClassName={"btn btn-primary gradientBg"}
                onClickFun={() => toggleOffcanvas()}
              >Create New Email Template</Button>
            </div>
            <EmailTemplatedata
              TemplateList={memoizedTemplateList}
              isLoading={getEmailTemplatePending}
              toggleOffcanvas={toggleOffcanvas}
              setSelectedTemplateData={setSelectedTemplateData}
              onHandleDeleteModal={onHandleDeleteModal}
              currentPage={currentPage}
              setCurrentPage={setCurrentPage}
              onHandleViewModal={onHandleViewModal}
            />
            <CreateAndUpdateEmailTemplate
              getAllEmailTemplalte={getAllEmailTemplalte}
              isOpenEmailType={isOpenEmailType}
              toggleOffcanvas={toggleOffcanvas}
              setSelectedTemplateData={setSelectedTemplateData}
              selectedTemplateData={selectedTemplateData}
              onHandleViewModals={onHandleViewModals}
            />
          </div>
          <div className="tab-pane fade active" id="addAction">
            <div className="cardHead d-flex align-items-center justify-content-between">
              <Button btnClassName={"btn btn-primary gradientBg"}
                onClickFun={() => toggleOpenAction()}
              >Add Action</Button>
            </div>

            <EmailActionData
              ActionList={memoizedActionList?.data}
              isLoading={getEmailTemplatePending}
              toggleOpenAction={toggleOpenAction}
              setSelectedActionData={setSelectedActionData}
              onHandleDeleteAction={onHandleDeleteAction}
              currentPage={currentPage}
              setCurrentPage={setCurrentPage}
              onHandleViewModal={onHandleViewModal}
            />

            <AddActionEmailTemplate
              TemplateList={memoizedTemplateList}
              getAllAction={getAllAction}
              toggleOpenAction={toggleOpenAction}
              openAction={openAction}
              setSelectedActionData={setSelectedActionData}
              selectedActionData={selectedActionData}
              onHandleViewModals={onHandleViewModals}
            />

          </div>
          {/* <div className="tab-pane fade active" id="addEmailType">
            <div className="cardHead d-flex align-items-center justify-content-between">
              <Button btnClassName={"btn btn-primary gradientBg"}
                onClickFun={() => toggleOpenEmailType()}
              >Add Email Type</Button>
            </div>

            <EmailTypeData
              ActionList={memoizedActionList?.data}
              isLoading={getEmailTemplatePending}
              toggleOpenAction={toggleOpenEmailType}
              setSelectedActionData={setSelectedActionData}
              onHandleDeleteAction={onHandleDeleteAction}
              currentPage={currentPage}
              setCurrentPage={setCurrentPage}
              onHandleViewModal={onHandleViewModal}
            />

            <CreateAndUpdateEmailType
              TemplateList={memoizedTemplateList}
              getAllAction={getAllAction}
              toggleOpenAction={toggleOpenEmailType}
              openEmailType={openEmailType}
              setSelectedActionData={setSelectedActionData}
              selectedActionData={selectedActionData}
              onHandleViewModals={onHandleViewModals}
            />

          </div> */}
        </div>
        {isActionDelete && (
        <ApproveRejectModal
          onApproveReject={onDeleteActionFunction}
          isShow={isActionDelete}
          desc={"You want to delete this Action"}
          btnRightLoading={DeleteActionTemplatePending}
        />)}
      {isShowDeleteModal && (
        <ApproveRejectModal
          onApproveReject={onDeleteCancelDepartment}
          isShow={isShowDeleteModal}
          desc={"You want to delete this Email Template"}
          btnRightLoading={deleteEmailTemplatePending}
        />)
      }


    </div>
  )
}

export default EmailTemplate
